<template>
	<section class="todoapp" :class="{ 'disable-todos': disableToDos }">
		<header class="header">
			<!--<h1>todos</h1>-->
			<input class="new-todo"
				autofocus autocomplete="off"
				placeholder="What needs to be done?"
				v-model="newTodo"
				@keyup.enter="addTodo">
		</header>
		<section class="main" v-show="allToDos.length" v-cloak>
			<ul class="todo-list">
				<li v-for="(todo, i) in visibleTasks"
					@click="toggleTaskStatus(todo.id)"
					class="todo"
					:key="`${i}-${todo.id}`"
					:class="{ completed: todo.isCompleted, editing: todo == editedTodo }">
					<div class="view">
						<input class="toggle" type="checkbox" @click.prevent v-model="todo.isCompleted">
						<label @dblclick="editTodo(todo)">{{ todo.title }}</label>
					</div>
					<input class="edit" type="text"
						v-model="todo.title"
						v-todo-focus="todo == editedTodo"
						@blur="doneEdit(todo)"
						@keyup.enter="doneEdit(todo)"
						@keyup.esc="cancelEdit(todo)">
				</li>
			</ul>
		</section>
		<footer class="footer" v-show="allToDos.length" v-cloak>
			<span class="todo-count">
				<strong>{{ remaining }}</strong> {{ remaining | pluralize }} left
			</span>
			<ul class="filters">
				<li><a @click="manageVisibility('all')" :class="{ selected: visibility == 'all' }">All</a></li>
				<li><a @click="manageVisibility('active')" :class="{ selected: visibility == 'active' }">Active</a></li>
				<li><a @click="manageVisibility('completed')" :class="{ selected: visibility == 'completed' }">Completed</a></li>
			</ul>
		</footer>
	</section>
</template>

<script>
	import { MemoryAccount, Node, Universal } from '@aeternity/aepp-sdk/es';
	// import Detector from '@aeternity/aepp-sdk/es/utils/aepp-wallet-communication/wallet-detector';
	// import BrowserWindowMessageConnection from '@aeternity/aepp-sdk/es/utils/aepp-wallet-communication/connection/browser-window-message';
	import contractDetails from '../config/contractDetails';
	import config from '../config/config';

	// const nodeUrl = 'https://mainnet.aeternity.io';
  // const compilerUrl = 'https://latest.compiler.aepps.com';

	export default {
    // app initial state
		data() {
			return {
				visibleTasks: [],
				newTodo: '',
				editedTodo: null,
				visibility: 'all',
				callOpts: {
					deposit: 0,
					gasPrice: 1000000000,
					amount: 0,
					fee: null, // sdk will automatically select this
					gas: 1000000,
					callData: '',
					verify: true
				},
				contractInstance: null
			}
		},
		props: {
			disableToDos: Boolean
		},
		computed: {
			allToDos() {
				return this.$store.getters.toDos;
			},
			doneToDos() {
				return this.$store.getters.doneToDos;
			},
			pendingToDos() {
				return this.$store.getters.pendingToDos;
			},
			remaining: function () {
				return this.$store.getters.remainingToDos;
			}
		},

		filters: {
			pluralize: function (n) {
				return n === 1 ? 'item' : 'items'
			}
		},

		// methods that implement data logic.
		// note there's no DOM manipulation here at all.
		methods: {
			confirmDialog(method, params, { id }) {
        return Promise.resolve(window.confirm(`User ${ id } wants to run ${ method } ${ params }`))
			},
			
			async addTodo() {
				this.$store.dispatch('toggleLoading');
				try {
					const value = this.newTodo && this.newTodo.trim()
					if (!value) {
						return
					}

					const allToDos = this.allToDos;

					allToDos.push({
						id: allToDos.length > 0 ? allToDos.length : 0,
						title: value,
						completed: false
					});

					await this.contractInstance.call('add_todo', [value]);

					await this.getContractTasks();

					this.$store.dispatch('toggleLoading');
					this.newTodo = ''
				} catch (e) {
					console.log(e);
					this.$store.dispatch('toggleLoading');
				}
			},

			editTodo(todo) {
				this.beforeEditCache = todo.title;
				this.editedTodo = todo
			},

			doneEdit(todo) {
				if (!this.editedTodo) {
					return
				}
				this.editedTodo = null;
				todo.title = todo.title.trim();
				if (!todo.title) {
					this.removeTodo(todo)
				}
			},

			cancelEdit(todo) {
				this.editedTodo = null;
				todo.title = this.beforeEditCache
			},

			manageVisibility(visibilityMode) {
				switch (visibilityMode) {
					case 'active': {
						this.visibleTasks = this.pendingToDos;
						this.visibility = visibilityMode;
						break;
					}
					case 'completed': {
						this.visibleTasks = this.doneToDos;
						this.visibility = visibilityMode;
						break;
					}
					default: {
						this.visibleTasks = this.allToDos;
						this.visibility = 'all';
						break;
					}
				}
			},

			async toggleTaskStatus(key) {
				this.$store.dispatch('toggleLoading');

				try {
					await this.contractInstance.call('edit_todo_state', [this.allToDos[key - 1].id, !this.allToDos[key - 1].isCompleted]);
					this.$store.dispatch('toggleTaskStatus', key);
					this.$store.dispatch('toggleLoading');
				} catch (err) {
					this.$store.dispatch('toggleLoading');
					console.log(err);
				}
			},

			async getClient() {
				const getSdkInstance = async (secretKey, publicKey) => {
					const sdkInstance = await Universal({
						nodes: [{ 
							name: 'testnet', 
							instance: await Node({ 
								url: config.nodeUrl, 
								internalUrl: config.nodeUrl
							})
						}],
						accounts: [
							MemoryAccount({ 
								keypair: { 
									secretKey: secretKey, 
									publicKey: publicKey 
								} 
							})
						],
						compilerUrl: config.compilerUrl,
					});
					return sdkInstance;
				};

				try {
					this.client = await getSdkInstance(config.priv, config.pub);

					this.$store.dispatch('setAccount', this.client);
					this.contractInstance = await this.client.getContractInstance(contractDetails.contractSource, { contractAddress: contractDetails.contractAddress });
				} catch (err) {
					console.log(err);
				}
			},

      async getContractTasks() {
        const allToDosResponse = await this.contractInstance.call("get_todos", []);
				const allToDos = await allToDosResponse.decode();
				const parsedToDos = this.convertSophiaListToTodos(allToDos);
				
				this.$store.dispatch('setToDos', parsedToDos);
      },

			convertToTODO(data) {
				return {
					title: data.name,
					isCompleted: data.is_completed
				}
			},

			convertSophiaListToTodos(data) {
				let tempCollection = [];
				let taskId;

				for (let dataIndex in data) {
					let todoInfo = data[dataIndex];

					taskId = todoInfo[0];
					let todo = this.convertToTODO(todoInfo[1]);
					todo.id = taskId;

					tempCollection.push(todo);
				}

				return tempCollection;
			}
    },
		async created() {
			this.$store.dispatch('toggleLoading');

			await this.getClient();

			await this.getContractTasks();
			this.manageVisibility();

			this.$store.dispatch('toggleLoading');
		},

		// a custom directive to wait for the DOM to be updated
		// before focusing on the input field.
		// http://vuejs.org/guide/custom-directive.html
		directives: {
		'todo-focus':

			function (el, binding) {
				if (binding.value) {
						el.focus()
				}
			}
		}
  }
</script>

